<template>
  <div
    class="vue--picture-cropper__wrap"
    :class="{ 'vue--picture-cropper__wrap-round': presetMode.mode === 'round' }"
    :style="boxStyle"
  >
    <img class="vue--picture-cropper__img" :src="img" :id="imgId || null" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import Cropper from 'cropperjs'
import 'cropperjs/dist/cropper.css'

/**
 *  API
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
export let cropper: any = null
// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const croppers: any = []

/**
 * 
 */
const VuePictureCropper = defineComponent({
  name: 'VuePictureCropper',
  props: {
    // 
    boxStyle: {
      type: Object,
      required: false,
      default: () => ({}),
    },

    // id
    imgId: {
      type: String,
      required: false,
      default: '',
    },

    // src
    img: String,

    // 
    options: {
      type: Object,
      required: false,
      default: () => ({}),
    },

    // 
    presetMode: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },
  data() {
    return {
      cropper: null,
      mimeType: '',
    }
  },
  watch: {
    /**
     * 
     * 
     */
    img(v, ov): void {
      console.log({ v, ov })

      // 
      if (!this.cropper) {
        this.init()
        return
      }

      // 
      try {
        this.cropper.replace(this.img)
        this.getImgSuffix()
        this.updateInstance()
      } catch (e) {
        console.log(e)
      }
    },
  },
  beforeUnmount() {
    /**
     * 
     */
    if (this.cropper) {
      this.cropper.destroy()
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    /**
     * 
     */
    async init(): Promise<void> {
      // 
      await this.$nextTick()

      // DOM
      const check: number = window.setInterval(() => {
        // DOM
        const imgElement: HTMLImageElement = this.imgId
          ? document.querySelector(`#${this.imgId}`)
          : document.querySelector('.vue--picture-cropper__img')

        // DOM
        if (imgElement) {
          // Vue
          try {
            this.cropper = new Cropper(imgElement, this.options)

            // 
            window.clearInterval(check)

            // 
            this.updateInstance()

            // 
            this.getImgSuffix()

            // 
            imgElement.addEventListener('ready', () => {
              this.usePresetMode()
            })
          } catch (e) {
            console.log(e)
          }
        }
      }, 10)
    },

    /**
     * 
     */
    usePresetMode() {
      if (Object.prototype.toString.call(this.presetMode) !== '[object Object]')
        return

      const { mode, width, height } = this.presetMode
      switch (mode) {
        // 
        case 'fixedSize':
        case 'round': {
          this.cropper.setCropBoxData({
            width,
            height,
          })
          break
        }
      }
    },

    /**
     * 
     */
    updateInstance(): void {
      cropper = this.cropper
      cropper.getDataURL = this.getDataURL
      cropper.getBlob = this.getBlob
      cropper.getFile = this.getFile

      // let _cropper = null
      // _cropper = this.cropper
      // _cropper.getDataURL = this.getDataURL
      // _cropper.getBlob = this.getBlob
      // _cropper.getFile = this.getFile

      // croppers.push(_cropper)

      // if (this.imgId) {
      //   cropper = croppers.find((item: any) => item.element.id === this.imgId)
      // } else {
      //   cropper = _cropper
      // }
      // console.log('cropper', cropper)
      // console.log('this.cropper', this.cropper)
    },

    /**
     * 
     */
    updateResultOptions(options: { [key: string]: unknown } = {}): {
      [key: string]: unknown
    } {
      this.updateInstance()

      if (Object.prototype.toString.call(this.presetMode) !== '[object Object]')
        return

      const { mode, width, height } = this.presetMode
      switch (mode) {
        // 
        case 'fixedSize':
        case 'round': {
          options.width = width
          options.height = height
          break
        }
      }

      return options
    },

    /**
     * 
     */
    getImgSuffix(): void {
      // png
      if (this.presetMode.mode === 'round') {
        this.mimeType = 'image/png'
      }
      // 
      else {
        const imgArr: string[] = this.img.split(',')
        const imgInfo: string = imgArr[0]
        const imgMimeType: string = imgInfo.replace(/data:(.*);base64/, '$1')
        this.mimeType = imgMimeType
      }
    },

    /**
     * base64
     */
    getDataURL(options: { [key: string]: unknown } = {}): string {
      options = this.updateResultOptions(options)
      try {
        let croppedCanvas = this.cropper.getCroppedCanvas(options)
        if (this.presetMode.mode === 'round') {
          croppedCanvas = this.getRoundedCanvas(croppedCanvas)
        }

        const result: string = croppedCanvas.toDataURL(this.mimeType)
        return result
      } catch (e) {
        return ''
      }
    },

    /**
     * blob
     */
    async getBlob(
      options: { [key: string]: unknown } = {}
    ): Promise<Blob | null> {
      options = this.updateResultOptions(options)
      return new Promise((resolve) => {
        try {
          let croppedCanvas = this.cropper.getCroppedCanvas(options)
          if (this.presetMode.mode === 'round') {
            croppedCanvas = this.getRoundedCanvas(croppedCanvas)
          }

          croppedCanvas.toBlob((blob: Blob) => {
            resolve(blob)
          }, this.mimeType)
        } catch (e) {
          resolve(null)
        }
      })
    },

    /**
     * file
     */
    async getFile(options: { [key: string]: unknown } = {}): Promise<File> {
      return new Promise((resolve) => {
        ;(async () => {
          // 
          const { fileName: optFileName } = options
          const suffix: string = this.mimeType.replace(/image\//, '')
          const fileName: string = optFileName
            ? `${optFileName}.${suffix}`
            : `cropped-${Date.now()}.${suffix}`

          // 
          const blob: Blob = await this.getBlob(options)

          // 
          const file: File = new File([blob], fileName, {
            type: this.mimeType,
          })
          resolve(file)
        })()
      })
    },

    /**
     * 
     * @description 
     * @see https://fengyuanchen.github.io/cropperjs/examples/crop-a-round-image.html
     */
    getRoundedCanvas(sourceCanvas: HTMLCanvasElement) {
      const canvas = document.createElement('canvas')
      const context = canvas.getContext('2d')
      const { width, height } = sourceCanvas

      canvas.width = width
      canvas.height = height
      context.imageSmoothingEnabled = true
      context.drawImage(sourceCanvas, 0, 0, width, height)
      context.globalCompositeOperation = 'destination-in'
      context.beginPath()
      context.arc(
        width / 2,
        height / 2,
        Math.min(width, height) / 2,
        0,
        2 * Math.PI,
        true
      )
      context.fill()

      return canvas
    },
  },
})

export default VuePictureCropper
</script>

<style>
.vue--picture-cropper__wrap {
  width: 100%;
  height: 100%;
  margin: 0;
}
.vue--picture-cropper__img {
  display: block;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
.vue--picture-cropper__wrap-round .cropper-view-box,
.vue--picture-cropper__wrap-round .cropper-face {
  border-radius: 50%;
}
</style>
